    margin: 5px 0 0 0;
    font-family: "Poppins Light";
}

.profile-options .highlighted-btn {
    margin: 0 0 0 28px;
}

.profile-options .highlighted-btn:hover {
    margin: 0 0 0 28px;
    background-color: cyan;
    color: black;
}

.profile-picture {

    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    height: 380px;
    width: 380px;
    margin-left:8rem;
}

.profile-picture-background {
    height: 95%;
    width: 95%;
    background-image: url(data:image/jpeg;base64,);
    background-size: cover;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-position: center;

}

.profile-picture-background:hover {
    -webkit-transform: scale(1.07);
            transform: scale(1.07);
    transition: 0.2s ease-in-out;
}

.colz .colz-icon i {
    color: white;
    padding: 8px 8px 8px 0px;

}

@media screen and (max-width: 1110px) {
    .profile-picture {
        margin: 0 0px 80px;
    }

    .profile-details-name .primary-text {
        font-size: 12px;
    }

    .profile-options .btn {
        width: 120px;
        padding: 11px 0;
        z-index: 700;
    }
}

@media screen and (max-width: 910px) {
    .profile-picture {
        height: 320px;
        width: 320px;
        margin: 0 0 0 4px;
    }
}

@media screen and (max-width: 810px) {
    .profile-picture {
        margin: 0 !important;
    }

    .profile-parent {
        flex-direction: column-reverse;
        margin: 40px 0 0 0;
    }

    .profile-options {
        display: flex;
        justify-content: space-between;
    }

    profile-details {
        margin: 25px 0 0;
    }

    .profile-options .highlighted-btn {
        margin: 0 !important;
    }

    .profile-options, .profile-details-role {
        text-align: center;
    }


}

@media screen and (max-width: 375px) {
    .profile-picture {
        height: 275px;
        width: 275px;
        margin-top: -90px;
    }

    .profile-details {
        width:90%
    }


}
